<script>
  import { onMount } from "svelte";
  import { push, location } from "svelte-stack-router";
  import {
    SkipBackFill,
    SkipForwardFill,
    PlayFill,
    PauseFill,
    ChatQuoteLine,
    ChatQuoteFill,
    Message2Line,
    RadioLine,
    Heart2Line,
    Heart2Fill,
    DislikeLine,
    RepeatOneLine,
    ShuffleLine,
    PlayListLine,
    PlayListFill,
    SortDesc,
    HeartPulseLine,
  } from "svelte-remixicon";

  import { Picker, Progress } from "../components/base";
  import Lyric from "../components/Lyric.svelte";
  import SongList from "../components/SongList.svelte";

  import {
    getSongUrl,
    personalFM,
    likeThisSong,
    getLyric,
    fmTrash,
  } from "../api/song";

  import {
    playStatusStore,
    currentSongStore,
    currentPlayListStore,
    currentSongIndexStore,
    maxPlayToTopStore,
    playIsMaxStore,
    isFMPlayStore,
    FMPlayNextStore,
    FMPlayStore,
    currentLyricStore,
    mainCoverTypeStore,
    playRepeatModelStore,
    currentSongQualityStore,
  } from "../store/play";
  import {
    isLoginStore,
    isHomePageStore,
    currentDetailSongerIdStore,
    isShowCommentStore,
  } from "../store/common";
  import { userLikeSongIdsStore } from "../store/user";

  import {
    timeToMinute,
    songerListToStr,
    Toast,
    ripple,
  } from "../utils/common";

  export let currentTime = "0:00"; //å½“å‰æ’­æ”¾æ—¶é—´
  export let currentTimeLong = 0; //è¿›åº¦æ¡é•¿åº¦
  export let endTime = 0; //ç»“æŸæ—¶é—´

  let lyricDom;
  let listDom;
  let loveDom;
  let disloveDom;
  let repeatDom;
  let commentDom;
  let preDom;
  let playDom;
  let nextDom;
  let coverDom;

  let playBgDom;
  let touchMoveToTop = 0; //æ’­æ”¾å™¨é¡¶éƒ¨è·ç¦»æ•´ä¸ªè§†çª—é¡¶éƒ¨çš„è·ç¦»
  let touchStartY; //æ»‘åŠ¨å¼€å§‹æ—¶å€™è§¦æ‘¸ç‚¹Yåæ ‡
  let touchEndY; //æ»‘åŠ¨ç»“æŸæ—¶å€™è§¦æ‘¸ç‚¹Yåæ ‡
  let touchStartTime = 0; //æ»‘åŠ¨å¼€å§‹æ—¶é—´æˆ³
  let touchEndTime = 0; //æ»‘åŠ¨ç»“æŸæ—¶é—´æˆ³
  let playListDom;

  $: isLikeCurrentSong = $isLoginStore
    ? $userLikeSongIdsStore.includes($currentSongStore.id)
    : false; //æ˜¯å¦å–œæ¬¢å½“å‰æ­Œæ›²
  $: likeLoading = false; //ä¼˜åŒ–ç‚¹å‡»çº¢å¿ƒè¯·æ±‚æ—¶å€™loadingæ•ˆæœ
  $: noLikeLoading = false; //ä¼˜åŒ–FMç‚¹å‡»ä¸å–œæ¬¢çº¢å¿ƒè¯·æ±‚æ—¶å€™loadingæ•ˆæœ
  $: lyricLoading = false; //ä¼˜åŒ–ç‚¹å‡»æ­Œè¯è¯·æ±‚æ—¶å€™loadingæ•ˆæœ
  $: PickerShow = false;
  $: songers = $currentSongStore.ar;

  onMount(() => {
    maxPlayToTopStore.set(window.screen.height + "px");
    playIsMaxStore.set(false);
    mainCoverTypeStore.set("cover");
    if (lyricDom) ripple(lyricDom);
    if (listDom) ripple(listDom);
    if (loveDom) ripple(loveDom);
    if (disloveDom) ripple(disloveDom);
    if (repeatDom) ripple(repeatDom);
    if (commentDom) ripple(commentDom);
    if (preDom) ripple(preDom);
    if (playDom) ripple(playDom);
    if (nextDom) ripple(nextDom);
    if (coverDom) ripple(coverDom);
  });
  //æ¥æ”¶å­ç»„ä»¶ï¼ˆProgressï¼‰å‘é€æ¥çš„äº‹ä»¶ï¼ˆsetCurrentï¼‰ï¼Œè·å–å½“å‰è¿›åº¦ç‚¹çš„å€¼ï¼ˆevent.detail.currentï¼‰ã€‚
  function getCurrent(event) {
    window.audioDOM.currentTime =
      (event.detail.current / 100) * window.audioDOM.duration;
    currentTime = timeToMinute(window.audioDOM.currentTime);
    currentTimeLong =
      (window.audioDOM.currentTime / window.audioDOM.duration) * 100;
    endTime =
      "-" +
      timeToMinute(window.audioDOM.duration - window.audioDOM.currentTime);
    window.audioDOM.play();
    playStatusStore.set(true);
  }
  //æ¥æ”¶å­ç»„ä»¶ï¼ˆProgressï¼‰å‘é€æ¥çš„äº‹ä»¶ï¼ˆsetTimeCurrentï¼‰ï¼Œè·å–å½“å‰è¿›åº¦æ—¶é—´ç‚¹çš„å€¼ï¼ˆevent.detail.timeCurrent ï¼‰ã€‚
  function getTimeCurrent(event) {
    window.audioDOM.currentTime =
      (event.detail.timeCurrent / 100) * window.audioDOM.duration;
    currentTime = timeToMinute(window.audioDOM.currentTime);
    currentTimeLong =
      (window.audioDOM.currentTime / window.audioDOM.duration) * 100;
    endTime =
      "-" +
      timeToMinute(window.audioDOM.duration - window.audioDOM.currentTime);
  }
  function handleDown() {
    maxPlayToTopStore.set(window.screen.height + "px");
    playIsMaxStore.set(false);
    mainCoverTypeStore.set("cover");
  }
  //åˆ‡æ¢ä¸‹ä¸€é¦–
  function playNextFun() {
    if ($isFMPlayStore) {
      //æ­£åœ¨ç§äººFM
      getSongUrlFun($FMPlayNextStore, "next");
      mainCoverTypeStore.set("cover");
    } else {
      if ($currentSongIndexStore === $currentPlayListStore.length - 1) {
        Toast("å·²ç»æ˜¯æœ€åä¸€é¦–äº†");
      } else {
        if ($mainCoverTypeStore === "lyric") mainCoverTypeStore.set("cover");
        //éšæœºæ¨¡å¼
        if ($playRepeatModelStore === "shuffle") {
          // Math.floor(Math.random() * 21);
          let index = Math.floor(
            Math.random() * ($currentPlayListStore.length - 1)
          );
          getSongUrlFun($currentPlayListStore[index], "shuffle", index);
        } else {
          currentSongIndexStore.set($currentSongIndexStore + 1);
          getSongUrlFun($currentPlayListStore[$currentSongIndexStore], "next");
        }
      }
    }
  }
  //åˆ‡æ¢ä¸Šä¸€é¦–
  function playPreFun() {
    if ($currentSongIndexStore === 0) {
      Toast("å·²ç»æ˜¯ç¬¬ä¸€é¦–äº†");
    } else {
      if ($mainCoverTypeStore === "lyric") mainCoverTypeStore.set("cover");
      getSongUrlFun($currentPlayListStore[$currentSongIndexStore - 1], "pre");
    }
  }
  //è·å–æ­Œå•url
  async function getSongUrlFun(song, type, index) {
    const res = await getSongUrl(song.id);
    if (res.code === 200) {
      if (res.data[0].url) {
        song.url = res.data[0].url.replace(/^http:/, "https:");
        if (res.data[0].fee === 1 && res.data[0].freeTrialInfo != null) {
          currentSongQualityStore.set("è¯•å¬");
        } else if (res.data[0].type === "flac") {
          currentSongQualityStore.set("FLAC");
        } else {
          currentSongQualityStore.set(res.data[0].br);
        }
        currentSongStore.set(song);
        localStorage.setItem("currentSong", JSON.stringify(song));
        window.audioDOM.src = song.url;
        window.audioDOM.play();
        playStatusStore.set(true);
        if ($isFMPlayStore) {
          //ç§äººFM
          personalFMFun();
          FMPlayStore.set(song);
          currentSongStore.set(song);
          currentPlayListStore.set([$FMPlayStore]);
          currentSongIndexStore.set(0);
          localStorage.setItem("currentSong", JSON.stringify(song));
        } else {
          //åˆ‡æ¢ä¸‹ä¸€é¦–å’Œä¸Šä¸€é¦–ä¹‹åï¼Œä¿®æ”¹æ’­æ”¾æ­Œæ›²åœ¨æ’­æ”¾åˆ—è¡¨ä¸­çš„ä½ç½®
          if (type === "next") {
            if ($currentSongIndexStore !== $currentPlayListStore.length - 1) {
              getSongUrl($currentPlayListStore[$currentSongIndexStore + 1].id);
            }
          } else if (type === "shuffle") {
            //éšæœº
            if ($currentSongIndexStore !== $currentPlayListStore.length - 1) {
              currentSongIndexStore.set(index);
            }
          } else {
            //ä¸Šä¸€é¦–
            currentSongIndexStore.set($currentSongIndexStore - 1);
          }
        }
      } else {
        Toast(
          `ğŸ˜‚ æ— æ³•æ’­æ”¾ã€Œ${song.name}ã€ï¼å¯èƒ½æ˜¯ç‰ˆæƒåŸå› ......å§ï¼è¯·æ’­æ”¾ä¸‹ä¸€é¦–ã€‚`,
          2000
        );
      }
    }
  }
  //ç§äººFM
  async function personalFMFun() {
    const res = await personalFM();
    if (res.code === 200) {
      res.data[0].al = res.data[0].album;
      res.data[0].ar = res.data[0].artists;
      res.data[0].alia = res.data[0].alias;
      FMPlayNextStore.set(res.data[0]);
      getSongUrl(res.data[0].id);
    }
  }
  //ç‚¹å‡»çº¢å¿ƒ
  async function handleLoveFun() {
    if ($isLoginStore) {
      likeLoading = true;
      const res = await likeThisSong({
        id: $currentSongStore.id,
        like: !isLikeCurrentSong,
      });
      if (res.code === 200) {
        const ids = JSON.parse(localStorage.getItem("useLoveSongIds"));
        if (isLikeCurrentSong) {
          let i = ids.indexOf($currentSongStore.id);
          ids.splice(i, 1);
        } else {
          ids.unshift($currentSongStore.id);
        }
        likeLoading = false;
        userLikeSongIdsStore.set(JSON.stringify(ids));
        localStorage.setItem("useLoveSongIds", JSON.stringify(ids));
      } else {
        likeLoading = false;
      }
    } else {
      Toast("å½“å‰æœªç™»å½•");
    }
  }
  //FM ä¸å–œæ¬¢
  async function handleFMNoLoveFun() {
    noLikeLoading = true;
    const res = await fmTrash($currentSongStore.id);
    if (res.code === 200) {
      noLikeLoading = false;
      playNextFun();
    }
  }
  //åˆ‡æ¢æ­Œè¯æ˜¾ç¤º
  function changeLyricFun() {
    if ($mainCoverTypeStore !== "lyric") {
      getlyricFun();
    } else {
      mainCoverTypeStore.set("cover");
    }
  }
  //è¯·æ±‚æ­Œè¯
  async function getlyricFun() {
    if ($currentLyricStore.songId === $currentSongStore.id) {
      mainCoverTypeStore.set("lyric");
    } else {
      lyricLoading = true;
      const res = await getLyric($currentSongStore.id);
      if (res.code === 200) {
        lyricLoading = false;
        if (res.nolyric || res.needDesc || res.lrc.lyric === "") {
          Toast("ğŸ™ˆğŸ™ˆä¹ˆæœ‰æ­Œè¯å“¦!!ğŸ™ˆğŸ™ˆ");
          currentLyricStore.set({
            songId: null,
            lyric: null,
            tlyric: null,
          });
          localStorage.setItem(
            "currentLyric",
            JSON.stringify({
              songId: null,
              lyric: null,
              tlyric: null,
            })
          );
        } else {
          currentLyricStore.set({
            songId: $currentSongStore.id,
            lyric: res.lrc.lyric,
            tlyric: res.tlyric.lyric,
          });
          localStorage.setItem(
            "currentLyric",
            JSON.stringify({
              songId: $currentSongStore.id,
              lyric: res.lrc.lyric,
              tlyric: res.tlyric.lyric,
            })
          );
          mainCoverTypeStore.set("lyric");
        }
      } else {
        lyricLoading = false;
      }
    }
  }
  //åˆ‡æ¢æ’­æ”¾åˆ—è¡¨æ˜¾ç¤º
  function changeListFun() {
    if ($mainCoverTypeStore !== "list") {
      if ($currentSongIndexStore > 200) {
        let r = confirm(
          "å½“å‰æ’­æ”¾æ­Œæ›²åœ¨æ’­æ”¾åˆ—è¡¨ä¸­ä½ç½®è¶…è¿‡ 200ï¼Œæ˜¾ç¤ºæ’­æ”¾åˆ—è¡¨å°†è‡ªåŠ¨æ»šåŠ¨è‡³å½“å‰æ­Œæ›²ï¼ŒæçŸ­æ—¶é—´å†…æ¸²æŸ“å¤§é‡é¡µé¢ä¼šä¸¥é‡æ¶ˆè€—è®¾å¤‡æ€§èƒ½ğŸ˜ˆğŸ˜ˆã€‚å¯ä»¥è€ƒè™‘åˆ°æ­Œå•è¯¦æƒ…é¡µï¼ˆå·²åšäº†æ‡’åŠ è½½ï¼‰æŸ¥çœ‹åˆ—è¡¨å“¦ğŸ˜…ã€‚ğŸ¤”ç¡®å®šæ˜¾ç¤ºæ’­æ”¾åˆ—è¡¨å—ğŸ¤”ï¼Ÿ"
        );
        if (r === true) {
          if ($currentPlayListStore.length > 200) {
            Toast("åˆ—è¡¨å¤ªé•¿ï¼Œç­‰æˆ‘åŠ è½½...ğŸ¥±ğŸ¥±", 1000, () => {
              mainCoverTypeStore.set("list");
              setTimeout(() => {
                playListDom.scrollTop = 60 * $currentSongIndexStore;
              }, 100); //åšå»¶è¿Ÿå¤„ç†ï¼Œæ¸²æŸ“é¡µé¢ä¹‹åæ‰èƒ½è·å–DOMé«˜åº¦ï¼Œè®¡ç®—æ»šåŠ¨é«˜åº¦
            });
          } else {
            mainCoverTypeStore.set("list");
            setTimeout(() => {
              playListDom.scrollTop = 60 * $currentSongIndexStore;
            }, 100); //åšå»¶è¿Ÿå¤„ç†ï¼Œæ¸²æŸ“é¡µé¢ä¹‹åæ‰èƒ½è·å–DOMé«˜åº¦ï¼Œè®¡ç®—æ»šåŠ¨é«˜åº¦
          }
        }
      } else {
        if ($currentPlayListStore.length > 200) {
          Toast("åˆ—è¡¨å¤ªé•¿ï¼Œç­‰æˆ‘åŠ è½½...ğŸ¥±ğŸ¥±", 1000, () => {
            mainCoverTypeStore.set("list");
            setTimeout(() => {
              playListDom.scrollTop = 60 * $currentSongIndexStore;
            }, 100); //åšå»¶è¿Ÿå¤„ç†ï¼Œæ¸²æŸ“é¡µé¢ä¹‹åæ‰èƒ½è·å–DOMé«˜åº¦ï¼Œè®¡ç®—æ»šåŠ¨é«˜åº¦
          });
        } else {
          mainCoverTypeStore.set("list");
          setTimeout(() => {
            playListDom.scrollTop = 60 * $currentSongIndexStore;
          }, 100); //åšå»¶è¿Ÿå¤„ç†ï¼Œæ¸²æŸ“é¡µé¢ä¹‹åæ‰èƒ½è·å–DOMé«˜åº¦ï¼Œè®¡ç®—æ»šåŠ¨é«˜åº¦
        }
      }
    } else {
      mainCoverTypeStore.set("cover");
    }
  }
  // åˆ‡æ¢æ’­æ”¾å¾ªç¯æ¨¡å¼
  function changeRrpeatFun(e) {
    if ($playRepeatModelStore === "heart") {
      Toast("å¿ƒåŠ¨æ¨¡å¼æ— éœ€åˆ‡æ¢");
    } else {
      if ($playRepeatModelStore === "repeat") {
        playRepeatModelStore.set("shuffle");
      } else if ($playRepeatModelStore === "shuffle") {
        playRepeatModelStore.set("repeatOnce");
      } else {
        playRepeatModelStore.set("repeat");
      }
    }
  }
  //æ­Œè¯åŒºåŸŸç‚¹å‡»
  function lyricClickFun() {
    mainCoverTypeStore.set("cover");
  }
  //å»æ­Œæ›²è¯„è®ºé¡µé¢
  function toCommentFun() {
    mainCoverTypeStore.set("cover");
    maxPlayToTopStore.set(window.screen.height + "px");
    playIsMaxStore.set(false);
    if (!($location === "/comments")) {
      if ($isHomePageStore) isHomePageStore.set(false);
      push("/comments");
    }
  }
  //æ»‘åŠ¨å¼€å§‹
  function touchStart(e) {
    touchStartTime = new Date().getTime();
    touchStartY = e.changedTouches[0].clientY;
    touchMoveToTop = 0;
    maxPlayToTopStore.set(touchMoveToTop + "px");
  }
  //æ»‘åŠ¨ç»“æŸ
  function touchEnd(e) {
    touchEndTime = new Date().getTime();
    touchEndY = e.changedTouches[0].clientY;
    // ç§»åŠ¨è·ç¦»å°äº100ä¸è§¦å‘ä»»ä½•è¡Œä¸º
    // ç§»åŠ¨ç»“æŸæ—¶ï¼Œæ’­æ”¾å™¨é¡¶éƒ¨è·æ•´ä¸ªè§†çª—é¡¶éƒ¨çš„è·ç¦»å¤§äºå±å¹•é«˜åº¦ä¸€åŠçš„æ—¶å€™ï¼Œå¯ä»¥ä¸ç”¨è€ƒè™‘æ»‘åŠ¨é€Ÿåº¦ï¼Œç›´æ¥æœ€å°åŒ–æ’­æ”¾å™¨
    // ç§»åŠ¨è·ç¦»è¶…è¿‡100ï¼Œæ’­æ”¾å™¨é¡¶éƒ¨è·æ•´ä¸ªè§†çª—é¡¶éƒ¨çš„è·ç¦»è¿˜ä¸åˆ°ä¸€åŠçš„æ—¶å€™ï¼Œè€ƒè™‘é€Ÿåº¦ï¼Œé€Ÿåº¦è¶…è¿‡0.8ï¼Œç›´æ¥æœ€å°åŒ–ï¼›
    // é€Ÿåº¦å°äº0.3ï¼Œä¸åˆ¤æ–­ä¸ºæœ€å°åŒ–ï¼›0.3-0.8ä¹‹é—´ï¼Œå¦‚æœç§»åŠ¨è·ç¦»è¶…è¿‡250ï¼Œæœ€å°åŒ–ï¼Œä¸åˆ°250åˆ™ä¸è§¦å‘æœ€å°åŒ–ã€‚
    if (touchMoveToTop >= window.screen.height / 2) {
      //æœ€å°åŒ–
      maxPlayToTopStore.set(window.screen.height + "px");
      playIsMaxStore.set(false);
      mainCoverTypeStore.set("cover");
    } else if (touchEndY - touchStartY <= 100) {
      maxPlayToTopStore.set("0px");
    } else if (
      touchEndY - touchStartY > 100 &&
      (touchEndY - touchStartY) / (touchEndTime - touchStartTime) >= 0.8
    ) {
      maxPlayToTopStore.set(window.screen.height + "px");
      playIsMaxStore.set(false);
      mainCoverTypeStore.set("cover");
    } else if (
      touchEndY - touchStartY > 100 &&
      (touchEndY - touchStartY) / (touchEndTime - touchStartTime) <= 0.3
    ) {
      maxPlayToTopStore.set("0px");
    } else if (
      0.3 < (touchEndY - touchStartY) / (touchEndTime - touchStartTime) < 0.8 &&
      touchEndY - touchStartY > 250
    ) {
      maxPlayToTopStore.set(window.screen.height + "px");
      playIsMaxStore.set(false);
      mainCoverTypeStore.set("cover");
    } else {
      maxPlayToTopStore.set("0px");
    }
  }
  //æ»‘åŠ¨è¿‡ç¨‹
  function touchMove(e) {
    //é˜»æ­¢æ»‘åŠ¨æ—¶ç©¿é€é—®é¢˜
    e.preventDefault();
    e.stopPropagation();
    if (parseFloat(playBgDom.style["top"]) >= 0) {
      touchMoveToTop = e.changedTouches[0].clientY - touchStartY;
      maxPlayToTopStore.set(touchMoveToTop + "px");
    }
  }
</script>

<div
  bind:this={playBgDom}
  class="play-bg"
  style="background: url({$currentSongStore.al.picUrl.replace(
    /^http:/,
    'https:'
  ) + '?param=800y800'});top:{$maxPlayToTopStore}"
>
  <div class="play">
    <div class="top-box">
      <div
        class="down"
        on:click={handleDown}
        on:touchstart={touchStart}
        on:touchend={touchEnd}
        on:touchmove={touchMove}
      >
        <div class="down-line" />
      </div>
      {#if $mainCoverTypeStore === "cover"}
        <div
          class="cover"
          on:click={handleDown}
          on:touchstart={touchStart}
          on:touchend={touchEnd}
          on:touchmove={touchMove}
          bind:this={coverDom}
        >
          <div>
            <img
              style="width:{$playStatusStore
                ? '280px'
                : '240px'};height:{$playStatusStore ? '280px' : '240px'}"
              src={$currentSongStore.al.picUrl.replace(/^http:/, "https:") +
                "?param=800y800"}
              alt=""
              class="cover-img"
            />
          </div>
        </div>
      {:else if $mainCoverTypeStore === "lyric"}
        <div class="lyric-cover" on:click={lyricClickFun}>
          <Lyric maxHeight="54vh" />
        </div>
      {:else if !$isFMPlayStore && $mainCoverTypeStore === "list"}
        <div class="song-list-box" bind:this={playListDom}>
          <SongList songList={$currentPlayListStore} />
        </div>
      {/if}
    </div>
    <div class="control-box">
      <div class="info">
        <div class="song">
          <div class="name">
            {$currentSongStore.name}
            <span class="alia">
              {$currentSongStore.alia && $currentSongStore.alia.length > 0
                ? `(${$currentSongStore.alia[0]})`
                : ""}
            </span>
          </div>
          <div
            class="songer"
            on:click={() => {
              if (songers.length === 1) {
                if (songers[0].id != 0) {
                  mainCoverTypeStore.set("cover");
                  maxPlayToTopStore.set(window.screen.height + "px");
                  playIsMaxStore.set(false);
                  isHomePageStore.set(false);
                  currentDetailSongerIdStore.set(songers[0].id);
                  push(`/artist?id=${songers[0].id}`);
                }
              } else {
                PickerShow = true;
              }
            }}
          >
            {songerListToStr($currentSongStore.ar)}
            <span>
              {$currentSongStore.al.name === ""
                ? ""
                : `- ${$currentSongStore.al.name}`}
            </span>
          </div>
        </div>
        <div class="progress">
          <div class="line">
            <Progress
              on:setCurrent={getCurrent}
              on:setTimeCurrent={getTimeCurrent}
              current={currentTimeLong}
            />
          </div>
          <!-- æ—¶é—´æ˜¾ç¤º -->
          <div class="time">
            <div class="time-item start">{currentTime}</div>
            <div
              class="time-item quality"
              on:click={() => {
                mainCoverTypeStore.set("cover");
                maxPlayToTopStore.set(window.screen.height + "px");
                playIsMaxStore.set(false);
                if (!($location === "/brSelect")) {
                  if ($isHomePageStore) isHomePageStore.set(false);
                  push("/brSelect");
                }
              }}
            >
              <span class="quality-span">
                {$currentSongQualityStore === "FLAC" ||
                $currentSongQualityStore === "è¯•å¬"
                  ? $currentSongQualityStore
                  : $currentSongQualityStore
                      .toString()
                      .substring(
                        0,
                        $currentSongQualityStore.toString().length - 3
                      ) + "K"}
              </span>
            </div>
            <div class="time-item end">{endTime}</div>
          </div>
        </div>
      </div>
      <div class="tool">
        <!-- æ­Œè¯ -->
        <div
          class="tool-item lyric"
          on:click={changeLyricFun}
          bind:this={lyricDom}
        >
          {#if lyricLoading}
            <span style="line-height: 30px;">
              <embed
                width="24"
                height="24"
                src="/images/Ripple.svg"
                type="image/svg+xml"
              />
            </span>
          {:else if $mainCoverTypeStore === "lyric"}
            <ChatQuoteFill size="20" style="vertical-align: middle" />
          {:else}
            <ChatQuoteLine size="20" style="vertical-align: middle" />
          {/if}
        </div>
        {#if !$isFMPlayStore}
          <!-- æ’­æ”¾åˆ—è¡¨ -->
          <div
            class="tool-item list"
            on:click={changeListFun}
            bind:this={listDom}
          >
            {#if $mainCoverTypeStore === "list"}
              <PlayListFill size="20" style="vertical-align: middle" />
            {:else}
              <PlayListLine size="20" style="vertical-align: middle" />
            {/if}
          </div>
        {/if}
        {#if $isFMPlayStore && $isLoginStore}
          <!-- FM ä¸å–œæ¬¢ -->
          <div
            class="tool-item list"
            on:click={handleFMNoLoveFun}
            bind:this={disloveDom}
          >
            {#if noLikeLoading}
              <span style="line-height: 30px;">
                <embed
                  width="24"
                  height="24"
                  src="/images/Ripple.svg"
                  type="image/svg+xml"
                />
              </span>
            {:else}
              <DislikeLine size="20" style="vertical-align: middle" />
            {/if}
          </div>
        {/if}
        {#if $isLoginStore}
          <!-- å–œæ¬¢ -->
          <div
            class="tool-item love"
            on:click={handleLoveFun}
            bind:this={loveDom}
          >
            {#if likeLoading}
              <span style="line-height: 30px;">
                <embed
                  width="24"
                  height="24"
                  src="/images/Ripple.svg"
                  type="image/svg+xml"
                />
              </span>
            {:else}
              <span
                style="color:{isLikeCurrentSong
                  ? 'var(--primary-text-color)'
                  : '#fff'}"
              >
                {#if isLikeCurrentSong}
                  <Heart2Fill size="20" style="vertical-align: middle" />
                {:else}
                  <Heart2Line size="20" style="vertical-align: middle" />
                {/if}
              </span>
            {/if}
          </div>
        {/if}
        {#if !$isFMPlayStore}
          <!-- æ’­æ”¾æ¨¡å¼ -->
          <div
            class="tool-item mode"
            on:click={(e) => changeRrpeatFun(e)}
            bind:this={repeatDom}
          >
            {#if $playRepeatModelStore === "repeat"}
              <SortDesc size="20" style="vertical-align: middle" />
            {:else if $playRepeatModelStore === "repeatOnce"}
              <RepeatOneLine size="20" style="vertical-align: middle" />
            {:else if $playRepeatModelStore === "heart"}
              <span class:heart-beat={$playStatusStore}>
                <HeartPulseLine size="20" style="vertical-align: middle" />
              </span>
            {:else}
              <ShuffleLine size="20" style="vertical-align: middle" />
            {/if}
          </div>
        {/if}
        <!-- è¯„è®º -->
        {#if $isShowCommentStore != "0"}
          <div
            class="tool-item comment"
            on:click={toCommentFun}
            bind:this={commentDom}
          >
            <Message2Line size="20" style="vertical-align: middle" />
          </div>
        {/if}
      </div>
      <div class="control">
        <div
          class="con-item pre"
          on:click={() => {
            if (!$isFMPlayStore) playPreFun();
          }}
          bind:this={preDom}
        >
          {#if $isFMPlayStore}
            <RadioLine
              size="24px"
              style="vertical-align: middle;;height:80px"
            />
          {:else}
            <SkipBackFill
              size="40px"
              style="vertical-align: middle;height:80px"
            />
          {/if}
        </div>
        <div
          class="con-item pause"
          bind:this={playDom}
          on:click={() => {
            if ($playStatusStore) {
              window.audioDOM.pause();
              playStatusStore.set(false);
              localStorage.setItem("pauseTimes", new Date().getTime());
            } else {
              //è§£å†³é•¿æ—¶é—´ä¸æ’­æ”¾URLå¤±æ•ˆé—®é¢˜(æš‚å®š30åˆ†é’Ÿè¿‡æœŸ)
              if (
                (new Date().getTime() -
                  Number(localStorage.getItem("pauseTimes"))) /
                  1000 /
                  60 >
                30
              ) {
                window.audioDOM.src = `https://music.163.com/song/media/outer/url?id=${$currentSongStore.id}.mp3`;
              }
              window.audioDOM.play();
              playStatusStore.set(true);
            }
          }}
        >
          {#if $playStatusStore}
            <PauseFill size="80px" style="vertical-align: middle;height:80px" />
          {:else}
            <PlayFill size="80px" style="vertical-align: middle;height:80px" />
          {/if}
        </div>
        <div class="con-item next" on:click={playNextFun} bind:this={nextDom}>
          <SkipForwardFill
            size="40px"
            style="vertical-align: middle;height:80px"
          />
        </div>
      </div>
    </div>
  </div>
</div>
{#if $playIsMaxStore && 0 < parseFloat($maxPlayToTopStore) < window.screen.height - 40}
  <div
    class="play-mask"
    style="background-color: rgba(0, 0, 0, {0.9 -
      parseFloat($maxPlayToTopStore) / window.screen.height});"
  />
{/if}

<Picker
  isShow={PickerShow}
  title="è¯·é€‰æ‹©æ­Œæ‰‹"
  on:ChangeShow={(e) => {
    PickerShow = e.detail.isShow;
  }}
  on:PickerClick={(e) => {
    const { item: artist } = e.detail;
    if (artist.id != 0) {
      mainCoverTypeStore.set("cover");
      maxPlayToTopStore.set(window.screen.height + "px");
      playIsMaxStore.set(false);
      isHomePageStore.set(false);
      currentDetailSongerIdStore.set(artist.id);
      push(`/artist?id=${artist.id}`);
    }
  }}
  list={songers}
/>

